<template>
    <div class='root'>
        <CateItem  v-for="(item,i) in cateList" :cateItem='item' :key='i'></CateItem>
    </div>
</template>
<script>
    import CateItem from './CateItem'
    export default{
        props:['cateList'],
        components:{
            CateItem,
        }
    }
</script>
<style scoped>
  .root{
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around;
      height: calc(100vh - 50px); 
      overflow: scroll;
  }
</style>


