<template>
    <div>
        <Navbar>全部分类</Navbar>
        <CateList :cateList='cateList'></CateList>
    </div>
</template>

<script>
    import CateList from '../components/CateList'
    export default{
        data:function(){
            return {
                cateList:[]
            }
        },
        components:{
            CateList,
        },
        mounted:function(){
            this.requestCate()
        },
        methods:{
            requestCate:function(){
                this.$ajax.get("/douyu/api/RoomApi/game")
                .then(res=>{
                    this.cateList = res.data.data
                })
            }
        }
    }
</script>

<style>

</style>


