// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import Navbar from './components/Navbar'
import axios from 'axios'
import {numFormat} from './util/filter.js'  // 导入过滤器

Vue.config.productionTip = false

/*全局注册导航条组件*/
Vue.component('Navbar',Navbar)

/*配置axios*/
Vue.prototype.$ajax = axios

/*全局注册过滤器*/
Vue.filter('numFormat',numFormat)

new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})
/*
页面适配方案：
目的：为了让页面内容适应不同尺寸屏幕的手机，页面中的元素的宽高不能写成固定值
解决：使用rem作为单位，当页面启动时，根据窗口的宽度计算出何时的rem的大小，当窗口变化时，再次计算rem大小，那么页面中的内容根据窗口的大小变化。
*/
var w = document.documentElement.clientWidth
document.documentElement.style.fontSize = w / 32 + 'px'

// 监听窗口大小改变
window.onresize  =function(){
  var w = document.documentElement.clientWidth
  document.documentElement.style.fontSize = w / 32 + 'px'
}


